$color_1: #181C24;
$color_2: #1B252C;
$font-family_1: "Poppins", sans-serif;
$font-family_2: 'Righteous';
$background-color_1: #fff;

@import "https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap";


.navbar {
	width: 100%;
	height: 48px;
	box-shadow: 0 1px 4px rgb(146 161 176 / 15%);
	.menu-items {
		display: flex;
		padding-top: 120px;
		box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
		height: 100vh;
		width: 100%;
		transform: translate(-150%);
		display: flex;
		flex-direction: column;
		margin-left: -40px;
		padding-left: 50px;
		transition: transform 0.5s ease-in-out;
		text-align: center;
		li {
			margin-bottom: 1.2rem;
			font-size: 1.5rem;
			font-weight: 500;
		}
	}
	.nav-container {
		li {
			list-style: none;
		}
		a {
			text-decoration: none;
			color: $color_1;
			
			font-size: 2.2rem;
			
			&:hover {
				font-weight: bolder;
			}
		}
	}
}
.nav-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 62px;
	display: block;
	position: relative;
	height: 60px;
	.checkbox {
		position: absolute;
		display: block;
		height: 32px;
		width: 32px;
		top: 20px;
		left: 20px;
		z-index: 5;
		opacity: 0;
		cursor: pointer;
	}
	.hamburger-lines {
		display: block;
		height: 26px;
		width: 32px;
		position: absolute;
		top: 17px;
		left: 20px;
		z-index: 2;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		.line {
			display: block;
			height: 4px;
			width: 100%;
			border-radius: 10px;
			background: #FFFFFF;
		}
		.line1 {
			transform-origin: 0% 0%;
			transition: transform 0.4s ease-in-out;
		}
		.line2 {
			transition: transform 0.2s ease-in-out;
		}
		.line3 {
			transform-origin: 0% 100%;
			transition: transform 0.4s ease-in-out;
		}
	}
	input[type="checkbox"] {
		&:checked~.menu-items {
			transform: translateX(0);
			top: 0;
			position: absolute;
			width: 100%;
			z-index: 1;
			margin: 0;
			left: 0;
			background-color: #0b0bda;
			height: 533px;

		}
		&:checked~.hamburger-lines {
			.line1 {
				transform: rotate(45deg);
			}
			.line2 {
				transform: scaleY(0);
			}
			.line3 {
				transform: rotate(-45deg);
			}
		}
		&:checked~.logo {
			display: none;
		}
	}
}
.logo {
	position: absolute;
	top: 5px;
	right: 15px;
	font-size: 1.2rem;
	color: $color_1;
}
.logo1 {
	position: relative;
	bottom: 25px;
}
.hamberger__logo {
	a {
		span {
			text-decoration: none;
			font-size: 30px;
			color: #ffff;
			font-family: $font-family_2;
			letter-spacing: 5px;
			font-weight: lighter;
			text-decoration: none;
			
		
		}
		&:hover {
			text-decoration: none;
		}
	}
}
.cartCount1 {
    background-color:black;
    border: none;
    color: white;
    border-radius: 6px;
    padding: 0px 4px;
    font-size: 11px;
    position: relative;
    top: -6px;
    right: 10px;
}
svg{
	color: white;
}
