@media only screen and (min-width: 320px) and (max-width: 786px) {
	.location {
		font-size: 35px;
		color: #f3f3f3;
		position: relative;
		bottom: 166px;
		right: 178px;
	}
	.hamberger__logo {
		left: 38%;
		position: absolute;
		top: 10px;
		margin-top: 5px;
		margin-left: 25px;
	}
	.hamburgerPhone {
		display: flex;
		background-color: #1363DF;
	}
	.headerHamburger {
		display: none;
	}
	.logo1 {
		text-align: center;
	}
	.header__icons {
		position: absolute;
		top: 15px;
		right: 5px;
	}
	.navbar {
		.menu-items {
			background-color: $background-color_1;
		}
	}
	.hamberger__logo  img{
width:80px;
	}
	
}
@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) {
	.logo1 {
		text-align: center;
	}
	.hamberger__logo {
		left: 40%;
		position: absolute;
		top: 10px;
	}
}
