/*# sourceMappingURL=Productdetails.css.map */
h1 {
  font-family: "Source Serif Pro", serif;
}

h2 {
  font-family: "Source Serif Pro", serif;
}

h3 {
  font-family: "Source Serif Pro", serif;
}

.checkbtn1 {
  width: 40%;
  padding: 5px;
  text-align: center;
  margin: auto;
  margin-top: 15px;
  background-color: #e26a2c;
  color: white;
}

.button-primary {
  background-color: #1f39ff;
  font-size: 14px;
  color: #ffffff;
  border-radius: 25px;
  text-transform: uppercase;
  border-color: transparent;
  padding: 5px 100px;
}

.productdetails {
  margin: 85px 0 25px 0;
}

.productdetails__left img {
  width: 100%;
}

.description {
  position: relative;
}
.description h2 {
  font-size: 36px;
  color: #172026;
  height: 80px;
  overflow: hidden;
}
.description h5 {
  font-size: 20px;
  color: #172026;
  font-weight: 600;
  margin: 20px 0;
}
.description p {
  color: #172026;
  font-size: 16px;
  padding-bottom: 20px;
}
.description P::after {
  content: "";
  background-color: #1f39ff;
  width: 15%;
  position: absolute;
  height: 5px;
  bottom: 0%;
  left: 0;
}

.productdetails__right {
  padding-left: 35px;
}
.productdetails__right h5 {
  font-size: 20px;
  color: #172026;
  margin-top: 20px;
  font-weight: 600;
}
.productdetails__right hr {
  margin: 10px 0;
}

.product-title {
  font-size: 36px;
  color: #172026;
}

.price {
  color: #172026;
  font-size: 20px;
}

.rating {
  font-size: 14px;
  color: #172026;
}

.productdetails__right-description {
  color: #172026;
  font-size: 12px;
}

.productdetails__right-space {
  height: 100px;
}

.productdetails__right-icons {
  display: flex;
}
.productdetails__right-icons p {
  width: 50%;
  font-size: 16px;
  margin-top: 10px;
}

.productdetails__right-colors img {
  margin-right: 10px;
  border-radius: 6px;
}
.productdetails__right-colors span:nth-child(1) img {
  background-color: #1aa7ec;
}
.productdetails__right-colors span:nth-child(2) img {
  background-color: #1363DF;
}
.productdetails__right-colors span:nth-child(3) img {
  background-color: #000;
}
.productdetails__right-colors span:nth-child(4) img {
  background-color: #808080;
  margin-right: 0;
}

.details {
  margin-top: 30px;
}

.quant {
  border: 2px #b5b7bc solid;
  width: 8rem;
  height: 4rem;
  text-align: center;
  border-radius: 6px !important;
  margin-top: 0.4rem;
}

.input-group {
  display: flex;
}
.input-group .border {
  width: 3rem;
  height: 3rem;
  background-color: transparent;
  border: 2px #b5b7bc solid !important;
  color: #b5b7bc;
  margin-top: -6px;
}

.addtocart {
  width: 300px;
  height: 40px;
  background: var(--primary-brand) 0% 0% no-repeat padding-box;
  background: #1363DF 0% 0% no-repeat padding-box;
  border-radius: 28px;
  opacity: 1;
}

.productdetails__right-size button {
  border: 2px solid #91959c;
  border-radius: 6px;
  background-color: #ffffff;
  margin-right: 5px;
  padding: 5px 15px;
}

.productdetails__right-quantity {
  margin-bottom: 30px;
}
.productdetails__right-quantity button {
  background: #eaebeb;
  border: 2px solid #b5b8ba;
  border-radius: 50%;
  height: 32px;
  width: 32px;
}
.productdetails__right-quantity input {
  border: 2px solid #91959c;
  border-radius: 8px;
  width: 114px;
  height: 40px;
  text-align: center;
  margin: 0 10px;
}

.share-icons ul {
  display: flex;
  margin-top: 15px;
}
.share-icons ul li {
  list-style-type: none;
  margin-right: 20px;
}
.share-icons ul li a {
  text-decoration: none;
  color: #172026;
}
.share-icons ul li a i {
  font-size: 20px;
  vertical-align: middle;
}

.productdetails__right-breadcrumb.mobile {
  display: none;
}

@media only screen and (min-width: 320px) and (max-width: 767px) {
  .input-group {
    padding-top: 0.3rem;
  }
  .description P::after {
    content: "";
    background-color: #1f39ff;
    width: 28%;
    position: absolute;
    height: 5px;
    bottom: 0%;
    left: 112px;
  }
  .productdetails_side {
    display: none;
  }
  .productdetails {
    margin: 20px 0;
  }
  .productdetails__right {
    padding-left: 0px;
  }
  .productdetails__right .button-primary {
    padding: 5px 95px;
  }
  .productdetails__right .pro-details {
    text-align: center;
  }
  .productdetails__right-title {
    font-size: 24px;
  }
  .productdetails__right-space {
    height: 0;
  }
  .productdetails__right-quantity {
    text-align: center;
  }
  .description h2 {
    font-size: 24px;
  }
  .description h5 {
    text-align: center;
  }
  .share-icons {
    text-align: center;
  }
  .share-icons ul {
    display: inline-block;
    width: 100%;
  }
  .share-icons ul li {
    display: inline-block;
  }
  .productdetails__right-colors img {
    width: 64px;
  }
  .productdetails__right-breadcrumb {
    display: none;
  }
  .productdetails__right-breadcrumb.mobile {
    display: block !important;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .quant {
    border: 2px #B5B7BC solid;
    width: 8rem !important;
    height: 3rem !important;
    text-align: center;
    border-radius: 6px !important;
    padding-top: 5px;
    margin-top: 0.4rem;
  }
  .productdetails_side {
    display: none;
  }
}/*# sourceMappingURL=productDetail.css.map */
