.header {
  padding: 13px 0;
  background-color: #1363DF;
  position: fixed;
  height: 70px;
  top: 0;
  width: 100%;
  z-index: 100;
}

.header__content {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
}

.header__logo a span {
  position: absolute;
  left: 15%;
  text-decoration: none;
  font-size: 25px;
  color: #FFFFFF;
  font-family: "Righteous";
  letter-spacing: 5px;
  text-decoration: none;
}
.header__logo a:hover {
  text-decoration: none;
}

.header__logo {
  margin-top: 4px;
}

.logoo {
  text-decoration: none !important;
}
.logoo:hover {
  text-decoration: none !important;
}

.header__logo img {
  width: 75px;
}

.header__selected {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
}
.header__selected ul {
  display: flex;
  list-style-type: none;
}
.header__selected ul li a {
  text-decoration: none;
  color: #FFFFFF;
  margin-left: 15px;
  font-size: 16px;
  padding-bottom: 15px;
}
.header__selected ul li a:hover {
  color: #fff;
  border-bottom: 4px solid #fff;
}
.header__selected ul li a:focus {
  color: #fff;
  border-bottom: 4px solid #fff;
}
.header__selected ul li a:active {
  border-bottom: 4px solid #fff;
}
.header__selected ul li .active {
  border-bottom: 4px solid #fff;
}

.header__icons ul {
  align-items: center;
  display: flex;
  list-style-type: none;
  padding: 0;
  justify-content: flex-end;
  margin-bottom: 0;
}
.header__icons ul li a {
  margin-left: 20px;
  text-decoration: none;
  color: #FFFFFF;
  font-size: 16px;
}
.header__icons ul img {
  vertical-align: middle;
}
.header__icons ul .cartCount_zero {
  position: relative;
}
.header__icons ul .cartCount_zero a {
  font-size: 20px;
}
.header__icons ul .cartCount_zero span {
  padding-left: 5px;
  padding-top: 1px;
  position: absolute;
  top: -5px;
  right: -11px;
  background: #000;
  color: #fff;
  width: 17px;
  height: 17px;
  border-radius: 50px;
  font-size: 12px;
}

.search {
  margin-top: 6px;
}

@media only screen and (min-width: 1024px) and (max-width: 1800px) {
  .hamburgerPhone {
    display: none;
  }
}/*# sourceMappingURL=header.css.map */
